// React의 useState 훅 가져오기
import { useState } from 'react';

// CSS 파일과 Bootstrap 가져오기
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';

// React-Bootstrap에서 사용할 컴포넌트들 가져오기
import { Container, Nav, Navbar, Row, Col, Form } from 'react-bootstrap';

// src 폴더에서 이미지 하나 불러오기
import goods4 from './img/cont3_cake4.png'

// jQuery 기능이 있는 컴포넌트 가져오기
import UseJquery from './UseJquery';

// 상품 데이터 가져오기 (data.js에 있을 것)
import data from './data';

// 라우팅 관련 기능 (페이지 이동)
import { Routes, Route, Link } from 'react-router-dom';

// 라우팅용 페이지 컴포넌트 2개 가져오기
import Company from './routes/Company';
import Product from './routes/Product';


function App() {
  // 상품 데이터 state로 설정 (변경은 없지만 state로 넣음)
  let [goods] = useState(data);

  return (
    <div className="App">

      {/* 네비게이션 바 (상단 메뉴) */}
      <Navbar bg="dark" data-bs-theme="dark">
        <Container>
          <Navbar.Brand href="#home">
            {/* 로고 이미지 */}
            <img src="https://jellyzoo.github.io/ex_img/logo.png" alt='logo' />
          </Navbar.Brand>
          <Nav className="me-auto">
            {/* 메뉴 링크들 (a태그처럼 동작) */}
            <Nav.Link href="#pascucci">파스쿠찌</Nav.Link>
            <Nav.Link href="#menu">메뉴</Nav.Link>
            <Nav.Link href="#info">매장안내</Nav.Link>
            <Nav.Link href="#notice">공지사항</Nav.Link>
          </Nav>
        </Container>
      </Navbar>

      {/* 페이지 이동 기능 (라우팅 영역) */}
      <Routes>
        <Route path="/" />
        <Route path="/Company" element={<Company />} />
        <Route path="/Product" element={<Product />} />
      </Routes>


      {/* 메인 비주얼 배너 영역 */}
      <div className='main-bg'></div>

      {/* 상품 리스트 출력 영역 */}
      <Container>
        <Row>
          {/* 상품 데이터를 반복해서 Card 컴포넌트로 보여주기 */}
          {
            goods.map((a, i) => {
              return (
                <Card goods={goods[i]} i={i + 1}></Card>
              );
            })
          }
        </Row>
      </Container>
    </div>
  );
}


/* Card 컴포넌트 (상품 하나를 보여주는 컴포넌트) */
function Card(props) {
  return (
    <Col>
      {/* public 폴더 이미지 불러오기 (파일명에 숫자를 붙여서 자동으로 선택) */}
      <img src={process.env.PUBLIC_URL + "/img/cont3_cake" + props.i + ".png"}
        width="50%"
        alt='listImg1' />
      {/* 상품 제목과 가격 출력 */}
      <h4>{props.goods.title}</h4>
      <p>{props.goods.price}원</p>
    </Col>
  )
}



export default App;


