import { Link } from "react-router-dom";

const Product = () => {
    return (
        <div>
            <ul>
                <li>
                    <Link to="/Company" className="Company_a">회사소개</Link>
                </li>
                <li>
                    <Link to="/Product" className="Product_a">제품소개</Link>
                </li>
            </ul>
            <h1>제품소개</h1>
            <p>제품소개 페이지입니다.</p>
        </div>
    );
}

export default Product;